<template>
  <div class="app-container">
    <div id="actionChart" v-if="charLives" :style="{width: '600px', height: '600px'}"></div>
    <!-- 回到顶部 -->
    <el-backtop><i class="el-icon-top"></i></el-backtop>
  </div>
</template>

<script>
export default {
  data() {
    return {
      charLives: true
    }
  },
  filters: {
   
  },
  mounted(){
    this.drawLine();
  },
  created() {

  },
  methods: {
    drawLine(){
        // NProgress.start(); //进度条开始
        // 基于准备好的dom，初始化echarts实例
        let actionChart = this.$echarts.init(document.getElementById('actionChart'))
        // 绘制图表
        actionChart.setOption({
            //图表标题
            title: { text: '' },
            //提示
            tooltip: {},
            //x轴展示数据链
            // xAxis: {
            //     data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
            // },
            // //y轴展示数据链
            // yAxis: {},
        //饼图切换样式：样式进行替换即可使用
        series: [
          {
            type: 'pie',
            data: [
              {
                value: 100,
                name: 'A'
              },
              {
                value: 200,
                name: 'B'
              },
              {
                value: 300,
                name: 'C'
              },
              {
                value: 400,
                name: 'D'
              },
              {
                value: 500,
                name: 'E'
              }
            ],
            //南丁格尔图
            roseType: 'area',
            //圆环图
            radius: ['40%', '70%']
          }
        ]
        });
        // NProgress.done(); //进度条结束
        // Axios异步加载数据 下面为参考实例，最好是后台拼接json数据到前台直接使用防止出现问题
        // $.get('data.json').done(function(data) {
        //   // 填入数据
        //   myChart.setOption({
        //     xAxis: {
        //       data: data.categories
        //     },
        //     series: [
        //       {
        //         // 根据名字对应到相应的系列
        //         name: '销量',
        //         data: data.data
        //       }
        //     ]
        //   });
        // })
    },
  },
  beforeDestroy(){
    
  }
}
</script>



